*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body {

    background-color: rgb(136, 238, 238);
    height: 50vh;
}

.container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.bicycle {
    background: rgb(243, 247, 242);
    width: 300px;
    height: 400px;
    margin: 20px;
    border-radius: 15px;
 
}

.bicycle-image {
    height: 200px;
    margin-bottom: 15px;
    background-size: cover;
    border-radius: 15px 15px 0 0;
}

.bc-1 {

    background-image: url(./bike\ 1.jpg);
}
.bc-2 {

    background-image: url(./bike\ 2.jpg);
}

.bc-3 {

    background-image: url(./bike\ 3.jpg);
}
.bc-4 {

    background-image: url(./bike\ 4.jpg);
}
.bicycle h2 {
    padding: 10px;
}

.bicycle p {
    padding: 10px;
}

.bicycle a {
    background-color: rgb(21, 168, 70);
    color: white;
    padding: 10px 20px;
    display: block;
    text-align: center;
    margin: 20px 50px;
}

.bicycle:hover {
    background-color: rgb(247, 204, 138);
    color: rgb(28, 29, 32);
    cursor: pointer;
    transform: scale(1.03);
    transition: all 1s ease;
}

.copyright {
    background-color: rgb(166, 162, 231);
    color: rgb(22, 21, 21);
    text-align: center;
    padding: 20px 10px 10px 20px;
    margin-bottom: 20px;
    line-height: 5.0;
}